import { formatDateMixin } from '../../utils/mixin.js'
export default {
	props: {
		newsList: {
			type: Array,
			default() {
				return []
			}
		}
	},
	methods: {
		navigator(id) {
			this.$emit('newsItemClick', id)
		}
	},
	mixins: [formatDateMixin]
}
